<script>
    import Form from "../../core/forms/form";
    import DataSelect from "../core/DataSelect";
    import MultiDataSelect from "../core/MultiDataSelect";
    import RolesSelect from "../roles/select";
    export default {
        name: "user-form",
        components: {RolesSelect, MultiDataSelect, DataSelect},
        props: [ 'edit' , 'id' ],
        data(){
            return {
                form : new Form({
                    receiver : '',
                    amount : '',
                    phone : '',
                    email : '',
                    ref : '',
                    description : '',
                },{

                }),
            };
        },
        mounted()
        {
            if (this.edit)
            {
                this.init();
            }
        },
        methods: {
            init(){
                this.form.loading = true;
                window.axios.get(`/admin/airtime/${this.id}/view`).then((response) => {
                    this.form.extract(response.data.body.model);
                    this.form.loading = false;
                });
            },
            create()
            {
                this.form.submit(this.edit ? `/admin/airtime/${this.id}/update`  : '/admin/airtime/create').then((response) => {
                    window.alerts.success(response).then((response) => {
                        if (!this.edit)
                            this.$router.push(`/admin/airtime/${response.data.body.model.id}/view`);
                    });
                }).catch((error) => {
                }).finally(() => {
                });
            }
        }
    }
</script>
<template>
    <div class="row">
        <div class="col-lg-12">
            <div :class="['dimmer' , form.loading ? 'active' : '']">
                <div class="loader"></div>
                <div class="dimmer-content">
                    <div class="card">
                        <div class="card-body">
                            <h4 class="mb-3"> {{ this.edit ? 'Edit' : 'Create' }}  Airtime</h4>
                            <div class="form-horizontal">
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Phone Number [Receiver]</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="meter" v-model="form.receiver" :class="[ 'form-control mw-400' , form.errors.get('receiver') ? 'is-invalid' : '' ]" placeholder="Phone Number">
                                        <div v-text="form.errors.get('receiver')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Amount</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="amount" v-model="form.amount" :class="[ 'form-control mw-400' , form.errors.get('amount') ? 'is-invalid' : '' ]" placeholder="Amount">
                                        <div v-text="form.errors.get('amount')" class="invalid-feedback"/>
                                    </div>
                                </div>

                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Phone</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="phone" v-model="form.phone" :class="[ 'form-control mw-400' , form.errors.get('phone') ? 'is-invalid' : '' ]" placeholder="Phone">
                                        <div v-text="form.errors.get('phone')" class="invalid-feedback"/>
                                    </div>
                                </div>


                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Email</label>
                                    <div class="col-lg-9">
                                        <input type="email" name="email" v-model="form.email" :class="[ 'form-control mw-400' , form.errors.get('email') ? 'is-invalid' : '' ]" placeholder="Email">
                                        <div v-text="form.errors.get('email')" class="invalid-feedback"/>
                                    </div>
                                </div>



                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Ref</label>
                                    <div class="col-lg-9">
                                        <input type="text" name="ref" v-model="form.ref" :class="[ 'form-control mw-400' , form.errors.get('ref') ? 'is-invalid' : '' ]" placeholder="Reference">
                                        <div v-text="form.errors.get('ref')" class="invalid-feedback"/>
                                    </div>
                                </div>



                                <div class="form-group row mb-3 align-items-center">
                                    <label class="col-lg-3 col-form-label">Description</label>
                                    <div class="col-lg-9">
                                        <textarea type="text" name="description" v-model="form.description" :class="[ 'form-control mw-400' , form.errors.get('description') ? 'is-invalid' : '' ]" placeholder="Description">

                                        </textarea>
                                        <div v-text="form.errors.get('description')" class="invalid-feedback"/>
                                    </div>
                                </div>
                                <div class="form-group mb-0 justify-content-end row">
                                    <div class="col-lg-9">
                                        <button type="submit" @click.prevent="create" :class="['btn btn-primary' , form.loading ? 'btn-loading' : '']">{{ this.edit ? 'Edit' : 'Create' }} Airtime</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
